<template>
  <main>
    <div class="container">  
      <div class="current">
        <img :src="projects[pointer].url" :alt="projects[pointer].url">
        <div class="info">
          <h1>PROJECT</h1>
          <p>-Name: {{ projects[pointer].name }}</p>
          <p>-Budget: {{ projects[pointer].budget }} â‚¬</p>
          <p>-Completed: {{ projects[pointer].completed }} %</p>
          <p>-Description: {{ projects[pointer].description }}</p>
          <router-link :to="'/project/' + projects[pointer].id">Click to see more</router-link> 
        </div>
        <div class="thumbnails">
          <button @click="prevSlide(pointer)">&lsaquo;</button> 
          <div class="thumbnail active" @click="goToSlide(0)">
            <img :src="projects[0].url" :alt = "projects[0].url">
          </div>
          <div class="thumbnail" @click="goToSlide(1)">
            <img :src="projects[1].url" :alt = "projects[1].url">
          </div>
          <div class="thumbnail" @click="goToSlide(2)">
            <img :src="projects[2].url" :alt = "projects[2].url">
          </div>
          <button @click="nextSlide(pointer)">&rsaquo;</button>
        </div>
      </div>
    </div>
    <p>Click on thumbnail or arrows to select project</p>
  </main>
</template>

<script>
export default {
  name: 'GalleryComp',
  data() {
    return {
      setInterval: true,
      pointer: 0,
      projects: [{
        id: "P1",
        name: "Portugal Project",
        budget: 2800,
        completed: 70,
        description: "Sed ut perspiciatis, unde omnis iste natus error sit voluptatem accusantium doloremque laudantium.",
        url: "https://images.unsplash.com/photo-1603657289433-e4983d2114b9?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1169&q=80"
      },
      {
        id: "P2",
        name: "Senegal Project",
        budget: 1900,
        completed: 10,
        description: "Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.",
        url: "https://images.unsplash.com/photo-1644772019005-5dc8e8ad66b7?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1167&q=80"
      },
      {
        id: "P3",
        name: "Camargue Project",
        budget: 2370,
        completed: 25,
        description: "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
        url: "https://images.unsplash.com/photo-1562747981-1975c624e26e?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1171&q=80"
      }]
    }
  },
  methods: {
    goToSlide(i) {
      this.setInterval = false
      this.pointer = i
      this.thumbnail(i)
    },
    prevSlide(i) {
      this.setInterval = false
      i--
      if (i < 0) i = 2
      this.pointer = i
      this.thumbnail(i)
    },
    nextSlide(i) {
      i++
      if (i > 2) i = 0
      this.pointer = i
      this.thumbnail(i)
    },
    thumbnail(i) {
      const thumbnails = document.getElementsByClassName("thumbnail")
      for (let t = 0; t < thumbnails.length; t++) {
        thumbnails[t].classList.remove("active")
      }
      thumbnails[i].classList.add("active")
    }
  }
}
</script>

